import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login'
import Home from '@/views/home'
import SchoolList from '@/views/admin/schoolList'
// import TeacherList from '@/views/admin/teacherList'
import ActivityList from '@/views/admin/activityList'
import NewsList from '@/views/admin/newsList'
import OrgSetting from '@/views/org/orgSetting'
import Course from '@/views/org/orgCourse'
import Teacher from '@/views/org/orgTeacher'
import Clazz from '@/views/org/orgClazz'
import Baoming from '@/views/org/orgBaoming'

Vue.use(Router)
const router = new Router({
  mode: 'history',
  scrollBehavior: () => ({
    y: 0
  }),
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'superadmin',
      children: [
        { path: '/school/list', component: SchoolList, name: '' }
        // { path: '/school/teacher', component: TeacherList, name: '' }
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'superadmin',
      children: [
        { path: '/activity',
          component: ActivityList,
          name: ''
        },
        { path: '/news', component: NewsList, name: '' }
      ]
    },
    {
      path: '/course',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'superadmin',
      children: [
        { path: '/course/content', component: Login, name: '' },
        { path: '/course/intro', component: Login, name: '' }
      ]
    },

    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'orgadmin',
      children: [{
        path: '/org/setting',
        name: '',
        type: 'orgadmin',
        component: OrgSetting
      }]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'orgadmin',
      children: [
        {
          path: '/org/course',
          name: '',
          type: 'orgadmin',
          component: Course
        },
        {
          path: '/org/teacher',
          name: '',
          type: 'orgadmin',
          component: Teacher
        },
        {
          path: '/org/class',
          name: '',
          type: 'orgadmin',
          component: Clazz
        }
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      iconCls: 'el-icon-message',
      type: 'orgadmin',
      children: [
        {
          path: '/org/signup',
          name: '',
          type: 'orgadmin',
          component: Baoming
        },
        {
          path: '/org/order',
          name: '',
          type: 'orgadmin',
          component: Login
        }
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.path === '/login') {
    sessionStorage.removeItem('user')
  }
  let user = JSON.parse(sessionStorage.getItem('user'))
  if (!user && to.path !== '/login') {
    next({ path: '/login' })
  } else {
    next()
  }
})

export default router



