<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px">
      <el-button size="small" type="primary" @click="handleAdd"></el-button>
    </el-col>

    <!---->
    <el-table class="ctable" :data="orgList" stripe border highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%">
      <el-table-column prop="indexOrder" label="" width="50
      ">
      </el-table-column>
      <el-table-column prop="name" label="" sortable>
      </el-table-column>
      <el-table-column prop="adminName" label="" sortable>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false" width="480px">
      <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="editForm.address" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telphone">
          <el-input v-model="editForm.telphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="adminname">
          <el-input v-model="editForm.adminname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="editForm.password" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false" width="480px">
      <el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="addForm.address" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telphone">
          <el-input v-model="addForm.telphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="adminname">
          <el-input v-model="addForm.adminname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="addForm.password" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import api from '../../api/index.js'

export default {
  data () {
    return {
      filters: {
        name: ''
      },
      orgList: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], // 

      editFormVisible: false, // 
      editLoading: false,
      editFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      },

      // 
      editForm: {
        name: '',
        address: '',
        telphone: null,
        adminname: '',
        password: ''
      },

      addFormVisible: false, // 
      addLoading: false,
      addFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        address: [{ required: true, message: '', trigger: 'blur' }],
        telphone: [{ required: true, message: '', trigger: 'blur' }],
        adminname: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      addForm: {
        name: '',
        address: '',
        telphone: null,
        adminname: '',
        password: ''
      }
    }
  },
  methods: {
    // 
    getOrgList () {
      this.listLoading = true
      api.orgListPage().then(res => {
        this.orgList = res.data.array
        this.listLoading = false
      })
    },

    // 
    handleDel (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        api.orgDel({groupId: row.groupId}).then((res) => {
          this.listLoading = false
          this.getOrgList()
        })
      }).catch(() => {})
    },

    // 
    handleEdit (index, row) {
      this.editFormVisible = true
      this.editForm = {
        groupId: this.orgList[index].groupId,
        name: this.orgList[index].name,
        address: this.orgList[index].address,
        telphone: this.orgList[index].telphone,
        adminname: this.orgList[index].adminName,
        password: this.orgList[index].password
      }
    },
    // 
    handleAdd () {
      this.addFormVisible = true
    },
    // 
    editSubmit () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.editLoading = true
          let para = Object.assign({}, this.editForm)
          api.orgEdit(para).then((res) => {
            this.editLoading = false
            this.$notify({
              message: '',
              type: 'success'
            })
            this.$refs['editForm'].resetFields()
            this.editFormVisible = false
            this.getOrgList()
          })
        }
      })
    },
    // 
    addSubmit () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.addLoading = true
          let para = Object.assign({}, this.addForm)
          api.orgCreate(para).then(res => {
            if (res.status === 'succ') {
              this.addLoading = false
              this.$notify({
                message: '',
                type: 'success'
              })
              this.$refs['addForm'].resetFields()
              this.addFormVisible = false
              this.getOrgList()
            } else {
              this.$message({
                message: res.message,
                type: 'error',
                duration: 0
              })
            }
          })
        }
      })
    },
    selsChange (sels) {
      this.sels = sels
    }
  },
  mounted () {
    this.getOrgList()
  }
}
</script>

<style scoped>
</style>



